import { isEmpty } from 'lodash';
import React, { Component } from 'react';
import {
  Row, Col, Button, Form, FormGroup, Label, Input,
  Modal, ModalHeader, ModalBody, ModalFooter
} from 'reactstrap';
import sgMail from '@sendgrid/mail';

import Image from 'components/Image';
import { getImage } from 'constants/images';

import styles from './Footer.module.scss';

// const fbMessenger = getImage('socials.facebook_messenger');

const socialData = [{
  icon: 'facebook',
  url: 'https://www.facebook.com/infinitefrontierstudio/',
}, {
  icon: 'instagram',
  url: 'https://www.instagram.com/infinitefrontier.sg/',
}, {
  icon: 'linkedin',
  url: 'https://www.linkedin.com/company/infinite-frontier/',
}]

const errorModal = {
  title: 'Oops',
  message: 'There is an error with the form. Please make sure the form is filled correctly. Please try again.',
};

const successModal = {
  title: 'Enquiry submitted',
  message: 'Thanks for enquiring with Infinite Frontier Studios. We will contact you via email.',
};

class Footer extends Component {
  state={
    email: '',
    name: '',
    message: '',
    showModal: false,
    modalData: errorModal,
  }

  handleSubmit = e => {
    if(e) e.preventDefault();

    const { email, name, message } = this.state;

    if (isEmpty(email) || isEmpty(name) || isEmpty(message)) {
      this.setState({
        showModal: true,
        modalData: errorModal,
      });

      return;
    }

    let isSuccess = false;

    // window.$.ajax({
    //   type: 'POST',
    //   url: 'https://mandrillapp.com/api/1.0/messages/send.json',
    //   data: {
    //     key: 'd4dc424b66bc0480e4311f2d68bbc927-us19',
    //     message: {
    //       from_email: email,
    //       to: [
    //           {
    //             email: 'wilson@infinitefrontier.sg',
    //             name: 'Wilson',
    //             type: 'to',
    //           }],
    //       autotext: true,
    //       subject: `Enquiry - ${name}`,
    //       html: 
    //         `<p>
    //           <span>Email: {email}</span>
    //           <br />
    //           <span>Name: {name}</span>
    //           <br />
    //           <span>Message: {message}</span>
    //           <br />
    //         </p>`
    //       ,
    //     }
    //   }
    //  }).done(function(response) {
    //     isSuccess = true;
    //  });
    try {
      sgMail.setApiKey(process.env.REACT_APP_SEND_GRID);
      const msg = {
        to: ['wilson@infinitefrontier.sg', 'support@infinitefrontier.sg'],
        from: email,
        subject: `Enquiry - ${name}`,
        html: 
            `<p>
              <span>Email: ${email}</span>
              <br />
              <span>Name: ${name}</span>
              <br />
              <span>Message: ${message}</span>
              <br />
            </p>`
          ,
      };
      sgMail.send(msg);
      isSuccess = true;
    } catch (error) {
      // eslint-disable-next-line
    }

    this.setState({
      showModal: true,
      modalData: isSuccess ? successModal : errorModal,
    }, () => {
      if (isSuccess) {
        this.setState({
          email: '',
          name: '',
          message: '',
        });
      }
    });
  }

  toggle = () => this.setState({ showModal: !this.state.showModal });

  renderEnquire() {
    const { email, name, message } = this.state;

    return (
      <div id="enquiry-form" className={styles.enquireContainer}>
        <div className={styles.enquireInner}>
          <h3>Achieve the Amazing</h3>
          <p>
          Thats all there is to it! Well be in touch every step of the way, keeping you in the loop and taking in your feedback without fail. Sound good? Great  get the ball rolling by contacting us today.
          </p>
          {/* <Button
            color="link"
            className={styles.messengerBtn}
            onClick={() => {
              try {
                window.FB.CustomerChat.showDialog();
              } catch(error) {
                //eslint-disable-next-line
              }
            }}
          >
            <Image {...fbMessenger} />
            <p>MESSAGE US!</p>
          </Button> */}
          <Form onSubmit={this.handleSubmit}>
            <FormGroup row>
              <Label className="text-left" for="email" sm={12}>Email</Label>
              <Col xs={12}>
                <Input onChange={e => this.setState({ email: e.target.value })} value={email} type="email" name="email" id="email" placeholder="john@email.com" />
              </Col>
            </FormGroup>
            <FormGroup row>
              <Label className="text-left" for="name" sm={12}>Name</Label>
              <Col xs={12}>
                <Input onChange={e => this.setState({ name: e.target.value })} value={name} type="text" name="name" id="name" placeholder="John Doe" />
              </Col>
            </FormGroup>
            <FormGroup row>
              <Label className="text-left" for="message" sm={12}>How can we help you?</Label>
              <Col xs={12}>
                <Input onChange={e => this.setState({ message: e.target.value })} value={message} type="textarea" name="message" id="message" placeholder="Help me get started on my explainer video for my business!" />
              </Col>
            </FormGroup>
            <FormGroup row>
              <Col xs={12}>
                <Button className={styles.messengerBtn}>Submit</Button>
              </Col>
            </FormGroup>
          </Form>
          <p className={styles.emailUs}>
            You may also email us at <a href="mailto:support@infinitefrontier.sg?Subject=Enquiry%20on%20Explainer%20Video%20Quotation" target="__blank">support@infinitefrontier.sg</a> to schedule a chat!
          </p>
        </div>
      </div>
    )
  }

  renderFooterInfo() {
    return (
      <div className={styles.footerInfo}>
        <Row className="main-margin w-100 align-items-start mb-4" noGutters>
          <Col className={styles.companyInfo} xs={{ size: 12, order: 2 }} sm={{ size: 7, order: 1}}>
            {/* <div className="d-flex">
              <p>HOME</p>
              <p>ABOUT US</p>
              <p>FAQ</p>
              <p>CHANGE LOG</p>
              <p>CONTACT</p>
            </div> */}
            <div className={styles.address}>
              <a href="https://goo.gl/maps/WEobHG1y5yM4xdjh9" target="__blank">
                10 ANSON ROAD<br/>
                #05-17<br/>
                INTERNATIONAL PLAZA<br/>
                SINGAPORE (079903) <i className="fas fa-map-marker-alt ml-1" />
              </a>
            </div>
            <div><p> 2019 INFINITE FRONTIER</p></div>
          </Col>
          <Col className="h-100" xs={{ size: 12, order: 1 }} sm={{ size: 5, order: 2}}>
            <div className={styles.socials}>
              {
                socialData.map(social => (
                  <Button
                    className={styles.socialBtn}
                    color="link"
                    onClick={() => { window.open(social.url, '_blank'); }}
                  >
                    <Image {...getImage(`socials.${social.icon}`)} />
                  </Button>
                ))
              }
            </div>
          </Col>
        </Row>
      </div>
    );
  }

  render() {
    const { showModal, modalData } = this.state;

    return (
      <div id="enquire" className={styles.footer}>
        {this.renderEnquire()}
        {this.renderFooterInfo()}
        <Modal centered className={styles.modal} isOpen={showModal} toggle={this.toggle}>
            <ModalHeader>{modalData.title}</ModalHeader>
            <ModalBody>
              <p>{modalData.message}</p>
            </ModalBody>
            <ModalFooter>
              <Button color="primary" onClick={this.toggle}>Close</Button>{' '}
            </ModalFooter>
        </Modal>
      </div>
    );
  }
}

export default Footer;
